import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { UserService } from '../../demo/service/userservice';
import { Car } from '../../demo/domain/car';
import { TreeNode, SelectItem} from 'primeng/api';

@Component({
    templateUrl: './roledetails.component.html',
    styles: [`
        .ui-dataview .search-icon {
            margin-top: 3em;
        }

        .ui-dataview .filter-container {
            text-align: center;
        }

        @media (max-width: 40em) {
            .ui-dataview .car-details, .ui-dataview .search-icon{
                text-align: center;
                margin-top: 0;
            }

            .ui-dataview .filter-container {
                text-align: left;
            }
        }
        .car-item {
            padding-top: 5px;
        }

        .car-item .p-md-3 {
            text-align: center;
        }

        .car-item .p-col-10 {
            font-weight: bold;
        }

        .empty-car-item-index {
            background-color: #f1f1f1;
            width: 60px;
            height: 60px;
            margin: 36px auto 0 auto;
            animation: pulse 1s infinite ease-in-out;
        }

        .empty-car-item-image {
            background-color: #f1f1f1;
            width: 120px;
            height: 120px;
            animation: pulse 1s infinite ease-in-out;
        }

        .empty-car-item-text {
            background-color: #f1f1f1;
            height: 18px;
            animation: pulse 1s infinite ease-in-out;
        }

        .title-container {
            padding: 1em;
            text-align: right;
        }

        .sort-container {
            text-align: left;
        }

        @media (max-width: 40em) {
            .car-item {
                text-align: center;
            }
            .index-col {
                display: none;
            }
            .image-col {
                display: none;
            }
        }
        @keyframes pulse {
            0% {
                background-color: rgba(165, 165, 165, 0.1)
            }
            50% {
                background-color: rgba(165, 165, 165, 0.3)
            }
            100% {
                background-color: rgba(165, 165, 165, 0.1)
            }
        }
    `],
    encapsulation: ViewEncapsulation.None
})
export class RoledetailsComponent implements OnInit {

    selectedCity1: string;

    roles = [
        {label: 'role1', value: 'r1'},
        {label: 'role2', value: 'r2'},
        {label: 'role3', value: 'r3'},
        {label: 'role4', value: 'r4'},
        {label: 'role5', value: 'r5'},
    ]

    cols: any[];

    cols2: any[];

    data: TreeNode[];

    selectedCar: Car;

    selectedCar2: Car;

    sourceUsers: Car[];

    targetUsers: Car[];

    orderListCars: Car[];

    carouselCars: Car[];


    events: any[];

    selectedNode1: TreeNode;

    selectedNode2: TreeNode;

    selectedNode3: TreeNode;

    selectedNodes: TreeNode[];

    fullcalendarOptions: any;

    sortOptions: SelectItem[];

    sortKey: string;

    sortField: string;

    sortOrder: number;

    timeout: any;

    constructor(private userService: UserService,) {}

    ngOnInit() {
        
        this.userService.getUsers().then(users => this.sourceUsers = users);
        this.targetUsers = [];
        

    }

    onSortChange(event) {
        const value = event.value;

        if (value.indexOf('!') === 0) {
            this.sortOrder = -1;
            this.sortField = value.substring(1, value.length);
        } else {
            this.sortOrder = 1;
            this.sortField = value;
        }
    }
}
