import { Component, ViewEncapsulation, OnInit, HostListener } from '@angular/core';
import { AppMainComponent } from './app.main.component';
import { AppMenuComponent } from './app.menu.component';
import { SideMenuService} from './services/side-menu.service';
import { componentRefresh } from '@angular/core/src/render3/instructions';


@Component({
  selector: 'app-topbar',
  templateUrl: './app.topbar.component.html',
  // encapsulation: ViewEncapsulation.None,
  providers: [
    AppMenuComponent,
  ]
})

export class AppTopBarComponent {

  isVisibleHome;
  isVisibleUsers;
  isVisibleProfile;
    
    constructor(public app: AppMainComponent, public appMenuComponent: AppMenuComponent, private sideMenuService: SideMenuService) {
      appMenuComponent.currentURL;
    }


    ngOnInit() {
      this.sideMenuService.emitVisibleHome.subscribe(isVisible => {
        this.isVisibleHome = isVisible;
      });
      this.sideMenuService.emitVisibleFile.subscribe(isVisible => {
        this.isVisibleUsers = isVisible;
      });
      this.sideMenuService.emitVisibleCharts.subscribe(isVisible => {
        this.isVisibleProfile = isVisible;
      });
    }

    setDashboardLeftMenu() {
      this.sideMenuService.setDashboardLeftMenu();
      console.log('Ustawiamy dashboaard' + this.isVisibleHome);
    }

    setAccountsLeftMenu() {
      this.sideMenuService.setAccountsLeftMenu();
      console.log('Ustawiamy accounts' + this.isVisibleUsers);
    }

    setProfileLeftMenu() {
      this.sideMenuService.setProfileLeftMenu();
      console.log('Ustawiamy profile' + this.isVisibleProfile);
    }

    logout() {    
      console.log('Wylogowano...');
    }

    

}
