import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { UserService } from '../../demo/service/userservice';
import { Car as User } from '../../demo/domain/car';
import { TreeNode, SelectItem} from 'primeng/api';
import { ConfirmationService } from 'primeng/primeng';

@Component({
    templateUrl: './users.component.html',
    styles: [`
        .ui-dataview .search-icon {
            margin-top: 3em;
        }

        .ui-dataview .filter-container {
            text-align: center;
        }

        @media (max-width: 40em) {
            .ui-dataview .car-details, .ui-dataview .search-icon{
                text-align: center;
                margin-top: 0;
            }

            .ui-dataview .filter-container {
                text-align: left;
            }
        }
        .car-item {
            padding-top: 5px;
        }

        .car-item .p-md-3 {
            text-align: center;
        }

        .car-item .p-col-10 {
            font-weight: bold;
        }

        .empty-car-item-index {
            background-color: #f1f1f1;
            width: 60px;
            height: 60px;
            margin: 36px auto 0 auto;
            animation: pulse 1s infinite ease-in-out;
        }

        .empty-car-item-image {
            background-color: #f1f1f1;
            width: 120px;
            height: 120px;
            animation: pulse 1s infinite ease-in-out;
        }

        .empty-car-item-text {
            background-color: #f1f1f1;
            height: 18px;
            animation: pulse 1s infinite ease-in-out;
        }

        .title-container {
            padding: 1em;
            text-align: right;
        }

        .sort-container {
            text-align: left;
        }

        @media (max-width: 40em) {
            .car-item {
                text-align: center;
            }
            .index-col {
                display: none;
            }
            .image-col {
                display: none;
            }
        }
        @keyframes pulse {
            0% {
                background-color: rgba(165, 165, 165, 0.1)
            }
            50% {
                background-color: rgba(165, 165, 165, 0.3)
            }
            100% {
                background-color: rgba(165, 165, 165, 0.1)
            }
        }
    `],
    encapsulation: ViewEncapsulation.None,
    providers: [ConfirmationService],
})
export class UsersComponent implements OnInit {

    btnDisabled: boolean = true;

    display1: boolean;
    display2: boolean;

    login: string;
    name: string;
    surname: string;
    password: string;
    email: string;
    role: string;

    users: User[];

    usersVirtual: User[] = [];

    cols: any[];

    cols2: any[];

    data: TreeNode[];

    selectedUser: User;

    sourceUsers: User[];

    targetUsers: User[];

    orderListUsers: User[];

    // files1: TreeNode[];

    // files2: TreeNode[];

    // files3: TreeNode[];

    // files4: TreeNode[];

    events: any[];

    selectedNode1: TreeNode;

    selectedNode2: TreeNode;

    selectedNode3: TreeNode;

    selectedNodes: TreeNode[];

    fullcalendarOptions: any;

    sortOptions: SelectItem[];

    sortKey: string;

    sortField: string;

    sortOrder: number;

    timeout: any;

    constructor(private userService: UserService, private confirmationService: ConfirmationService) {}

    btnEnabled() {
        if (this.selectedUser) {
            this.btnDisabled = false;
        }
    }

    ngOnInit() {

        this.userService.getUsers().then(users => this.users = users);

        this.userService.getUsers().then(users => this.sourceUsers = users);
        this.targetUsers = [];

        this.cols = [
            { field: 'id', header: 'Account ID' },
            { field: 'name', header: 'Name' },
            { field: 'surname', header: 'Surname' },
            { field: 'mail', header: 'Email' },
            { field: 'role', header: 'Role'},
        ];
    
    }

    onSortChange(event) {
        const value = event.value;

        if (value.indexOf('!') === 0) {
            this.sortOrder = -1;
            this.sortField = value.substring(1, value.length);
        } else {
            this.sortOrder = 1;
            this.sortField = value;
        }
    }

    confirmDelete() {
        this.confirmationService.confirm({
            message: 'Are you really want to delete this user?'
        });
    }

    
}
