import { Injectable, Output, EventEmitter } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SideMenuService {

  visibleHome = true;
  visibleFile = true;
  visibleCharts = true;

  @Output() emitVisibleHome: EventEmitter<boolean> = new EventEmitter();
  @Output() emitVisibleFile: EventEmitter<boolean> = new EventEmitter();
  @Output() emitVisibleCharts: EventEmitter<boolean> = new EventEmitter();

  setDashboardLeftMenu() {
    // this.visible = !this.visible;
    this.emitVisibleHome.emit(this.visibleHome);
    this.emitVisibleFile.emit(this.visibleFile);
    this.emitVisibleCharts.emit(this.visibleCharts);
  }

  setAccountsLeftMenu() {
    // this.visible = !this.visible;
    this.emitVisibleFile.emit(this.visibleFile);
    
  }

  setProfileLeftMenu() {
    // this.visible = !this.visible;
    this.emitVisibleCharts.emit(this.visibleCharts);
    
  }

}
