import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { UserService } from '../../demo/service/userservice';
import { Car as User } from '../../demo/domain/car';
import { TreeNode, SelectItem } from 'primeng/api';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-roles',
  templateUrl: './roles.component.html',
  styleUrls: ['./roles.component.css'],
  encapsulation: ViewEncapsulation.None,
  providers: [ConfirmationService],
})
export class RolesComponent implements OnInit {

  btnDisabled: boolean = true;

  rolename: string;
  desc: string;
  ad: string;

  display: boolean;

  roles: User[];

  cols: any[];

  cols2: any[];

  data: TreeNode[];


  orderListCars: User[];

  selectedRole: User;

  events: any[];

  sortOptions: SelectItem[];

  sortKey: string;

  sortField: string;

  sortOrder: number;

  timeout: any;

  constructor(private userService: UserService, private confirmationService: ConfirmationService) {}

  btnEnabled() {
    if (this.selectedRole) {
        this.btnDisabled = false;
    }
}

  ngOnInit() {

      this.cols = [
          { field: 'id', header: 'Role ID' },
          { field: 'description', header: 'Description' },
          { field: 'adcounterpart', header: 'AD Counterpart' }
      ];
      this.userService.getRoles().then(roles => this.roles = roles);
  }

  onSortChange(event) {
      const value = event.value;

      if (value.indexOf('!') === 0) {
          this.sortOrder = -1;
          this.sortField = value.substring(1, value.length);
      } else {
          this.sortOrder = 1;
          this.sortField = value;
      }
  }

  confirmDelete() {
    this.confirmationService.confirm({
        message: 'Are you really want to delete this role?'
    });
}

}
